@import url('https://fonts.googleapis.com/css2?family=Asap:wght@100;200;300;400;500;600;700&display=swap');

body{

  background-color: rgb(212, 222, 232);
}


.App {
  text-align: center;
  background-color: #fbfbfb;
  width: 390px;
  height: 850px;
  margin: 10px;
  border-radius: 20px;
  box-shadow: 3px 5px 5px 5px rgba(0, 0, 0, 0.2);
  font-family: 'Asap', sans-serif;
  
}

/*
Index
*/
.App > h1 {

  text-align: center;
  font-size: xx-large;
  margin-left: 10%;
  margin-right: 10%;
}

.Body{

  margin: 10%;

}

.App > h1 {

  margin-top:50px;
  color: #1B1E28;

}

.App > h1 > span {

  color: #FF7029;
}

.App > p {

  font-size: large;
  text-align: center;
  color: #7D848D;
  margin-left: 50px;
  margin-right: 50px;

}

.Divider {
  margin-left: 45%;
  width: 40px;

}


.Turquoise-Button {

  background-color: #48C3C0; /* Green */
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  width: 350px;
  border-radius: 15px;
  font-style: italic;
  margin-top: 60px;
}

.Turquoise-Button-Text{

  text-decoration: none!important; 
  text-decoration-color: white;
  color:white;

}

a:visited { text-decoration: none; color:#48C3C0; }
a { text-decoration: none; color:#48C3C0; }

.Login-Title{
  font-size: 100px;
  font-weight: bold;
}

.Gray-Input{
  background-color: #f0f0f0; /* Green */
  border: none;
  color: #282c3b;
  padding: 23px 32px;
  text-align: left;
  display: inline-block;
  font-size: 16px;
  width: 270px;
  border-radius: 15px;
  font-style: italic;
  margin: 10px;
  
}

.Small-Turqoise-Text{
  color:#48C3C0;
  margin-right: 30px;
  font-size: 15px;
}

.Small-Gray-Text{
  font-size: 15px;
  color: #7D848D;

}

.Align-Right{
  text-align: right;
}

.Login-Body{
  margin-top: 75px;
  margin-bottom: 10px;
}

.Turquoise-Button-Login{
  background-color: #48C3C0; /* Green */
  border: none;
  color: white;
  padding: 18px 32px;
  text-align: center;
  display: inline-block;
  font-size: 20px;
  width: 270px;
  border-radius: 15px;
  font-style: italic;
  margin: 10px;
}

.Align-Left{
  text-align: left;
  margin-left: 40px;
}
/*
I don't know what these are - leftover from the basic template
*/

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.during-popup{
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
}


/* POP UP */

.PopUp{
  position: fixed;
  left: 15%;
  top: 30%;

  width: 70%;
  
  border: 0px;
  border-radius: 1em;
  box-shadow: 1px 1px 120px 50px #f2f2f2;

  background-color: #ffffff;
  
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  }


.PopUp-Content-CreateSuite{
  width: 100%;
  display: inline-block;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}

.PopUp-Content-CopyInvite{
  width: 100%;
  display: inline-block;
  justify-content: center;
  align-items: center;
  margin:20px;
}

.Popup-Input{
  background-color: #f8f8f8; /* Green */
  border: none;
  color: #1B1E28;
  padding: 15px 17px;
  text-align: center;
  display: inline-block;
  font-size: 18px;
  border-radius: 18px;
  font-style: italic;
}

.Popup-Plus{
  color: white;
  width: 20px;
  height: 20px;
  background-color: #48C3C0;
  border-radius: 50%;
  border: 0px;
  margin-right: 10px;
  margin-left: 5px;

}

.Login-Building-Icon{
  text-align: left;
  margin-left:30px;
}


.Medium-Pink-Text{
  color: #C34889;
  font-size: 20px;

}